<template>
    <div>
        <g-appbar
            title="한국전력공사  T.B.M" 
            background="#f8f8fa"
            :left="true" :right="true"
        />
        <div class="g-area-top">
            <div class="d-flex align-center">
                <p class="g-step-now">{{ step }}</p>
                <div class="g-step-bar"></div>
                <p class="g-step">{{ totalStep }}</p>
            </div>
            <p class="s-title">
                위험성 평가 결과를<br/> 
                전파해 주세요.
            </p>
        </div>
        <div class="g-progress">
            <v-progress-linear 
                :model-value="step"
                :buffer-value="progress"
                color="#7285f3"
                background-opacity="1"
            ></v-progress-linear>
        </div>
        <div class="g-area-middle"> 
            <div class="g-voice">
                <div class="s-notice-box">
                    <img class="s-notice-icon" src="@/assets/images/icon/i_speaker.png"/>
                    <p>
                        아래의 <span class="font_bold">전달 사항</span>을 소리 내어 
                        <span class="font_bold"> 말씀해 주세요.</span>
                    </p>
                </div>
                <div class="s-content">
                    <span v-for="(item, index) in textArray" :key="index" class="ml-1">
                        <span :class="textStyle(item)" v-html="item.text"></span>
                    </span>
                </div>
            </div>
            <div class="g-fixed-voice-btn">
                <div class="g-fixed-voice-gradation">
                    <img class="g-fixed-voice-gradation" src="@/assets/images/voice/i_gradation_top.png"/>
                </div>
                <VoiceRecorderHidden 
                    ref="VoiceRecorder"
                    @checkAnalysisResult="checkAnalysisResult"
                    @midResultState="midResultState"
                />
                <basic-btn 
                    :state="voiceState"
                    @onClick="onClickNext"
                />
                <div v-if="voiceState === 'replay'">
                    <next-btn @onClick="onForceNext"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BasicBtn from '@/components/button/BasicBtn.vue'
import VoiceRecorderHidden from '@/components/common/VoiceRecorderHidden.vue'
import NextBtn from '@/components/button/NextBtn.vue'

export default {
    name: 'Step01',
    components: { BasicBtn, VoiceRecorderHidden, NextBtn },
    data() {
        return {
            step: 4,
            totalStep: 6,
            textArray: [
                { index: 1, text: '주요 ', check: false, require: true },
                { index: 2, text: '위험요인', check: false, require: true },
                { index: 3, text: '으로 ', check: true, require: false },
                { index: 4, text: '상부 ', check: false, require: true },
                { index: 5, text: '작업자 ', check: false, require: true },
                { index: 6, text: '추락', check: false, require: true },
                { index: 7, text: ', ', check: true, require: false },
                { index: 8, text: '활선부 ', check: false, require: true },
                { index: 9, text: '접근', check: false, require: true },
                { index: 10, text: '에 의한 ', check: true, require: false },
                { index: 11, text: '감전', check: false, require: true },
                { index: 12, text: '이 ', check: true, require: false },
                { index: 13, text: '있습니다.', check: true, require: false },
                { index: 14, text: '<br/><br/>', check: true, require: false },
                { index: 15, text: '상부작업자', check: false, require: true },
                { index: 16, text: '는 ', check: true, require: false },
                { index: 17, text: '안전대', check: false, require: true },
                { index: 18, text: '를 ', check: true, require: false },
                { index: 19, text: '반드시 ', check: true, require: false },
                { index: 20, text: '착용하시고,', check: true, require: false },
                { index: 21, text: '버켓조작부 ', check: false, require: true },
                { index: 22, text: '안전띠용 ', check: false, require: true },
                { index: 23, text: '로프걸이', check: false, require: true },
                { index: 24, text: '에', check: true, require: false },
                { index: 25, text: '체결해', check: true, require: false },
                { index: 26, text: '주시기 ', check: true, require: false },
                { index: 27, text: '바랍니다.', check: true, require: false },
                { index: 28, text: '<br/><br/>', check: true, require: false },
                { index: 29, text: '입상 ', check: false, require: true },
                { index: 30, text: '케이블 ', check: false, require: true },
                { index: 31, text: '방전 ', check: false, require: true },
                { index: 32, text: '상태를 ', check: true, require: false },
                { index: 33, text: '꼭 ', check: true, require: false },
                { index: 34, text: '확인해 ', check: true, require: false },
                { index: 35, text: '주시고, ', check: true, require: false },
                { index: 36, text: '변압기 ', check: false, require: true },
                { index: 37, text: '교체 ', check: false, require: true },
                { index: 38, text: '작업 ', check: true, require: false },
                { index: 39, text: '시 ', check: true, require: false },
                { index: 40, text: '활선구간이 ', check: true, require: false },
                { index: 41, text: '근접해 ', check: true, require: false },
                { index: 42, text: '있으니 ', check: true, require: false },
                { index: 43, text: '활선접근 ', check: false, require: true },
                { index: 44, text: '경보기', check: false, require: true },
                { index: 45, text: '를 ', check: true, require: false },
                { index: 46, text: '착용하고 ', check: true, require: false },
                { index: 47, text: '작업구간 ', check: true, require: false },
                { index: 48, text: '주의해 ', check: true, require: false },
                { index: 49, text: '주시기 ', check: true, require: false },
                { index: 50, text: '바랍니다.', check: true, require: false },
            ],

            searchKeywords: [
                { matchIndex: 1, texts: ['주요'] },
                { matchIndex: 2, texts: ['위험요인'] },
                { matchIndex: 4, texts: ['상부'] },
                { matchIndex: 5, texts: ['작업자'] },
                { matchIndex: 6, texts: ['추락', '추합', '추라'] },
                { matchIndex: 8, texts: ['활선부', '하일선부', '활산부'] },
                { matchIndex: 9, texts: ['접근'] },
                { matchIndex: 11, texts: ['감전', '관점', '감자', '감정'] },
                { matchIndex: 15, texts: ['상부작업자', '참작업자'] },
                { matchIndex: 17, texts: ['안전대', '단자대'] },
                { matchIndex: 21, texts: ['버켓조작부', '버킷저작품', '법킷조작품', '버킷조작부', '벗켓조작부호', '버킷조작법', '법해조작부'] },
                { matchIndex: 22, texts: ['안전띠용', '앉은디용', '안전비용', '완전띠용'] },
                { matchIndex: 23, texts: ['로프걸이', '로프거리에', '루프걸리', '로프걸리', '로프거래', '로프거리'] },
                { matchIndex: 29, texts: ['입상'] },
                { matchIndex: 30, texts: ['케이블', '케이별'] },
                { matchIndex: 31, texts: ['방전', '정자', '방점'] },
                { matchIndex: 36, texts: ['변압기', '변학교', '변합'] },
                { matchIndex: 37, texts: ['교체', '교차'] },
                { matchIndex: 43, texts: ['활선접근', '활선작품', '발선접근', '활산접근', '횟선접근'] },
                { matchIndex: 44, texts: ['경보기'] },
            ],
            voiceState: 'start',
        }
    },
    computed: {
        progress() {
            return Number((this.step) / (this.totalStep)) * 100;
        },
        allConfirm() {
            return this.voiceState === 'finish'
        }
    },
    methods: {
        onForceNext() {
            this.$router.push('/TBM/Step05')
        },
        onClickNext() {
            if (this.allConfirm) {
                this.$router.push('/TBM/Step05')
            } else if (this.voiceState === 'replay') {
                this.$refs.VoiceRecorder.startRecording()
            }
        },
        checkAnalysisResult(result) {
            const audio = result.replace(/\s/g, '');

            for (const keyword of this.searchKeywords) {
                for (const text of keyword.texts) {
                    if (audio.includes(text)) {
                        for (const operator of this.textArray) {
                            if (operator.index === keyword.matchIndex) {
                                operator.check = true
                                break
                            }
                        }
                    }
                }
            }
        },
        midResultState(result) {
            if (result === 'finish') {
                const allChecked = this.textArray.every((operator) => operator.check);
                if (!allChecked) {
                    result = 'replay'
                    this.$refs.VoiceRecorder.changeState(result)
                }
            }
            console.log('result', result)
            this.voiceState = result
        },
        textStyle(item) {
            if (item.require) {
                if (item.check) {
                    return 'color_primary_4 font_extra_bold'
                } else {
                    return 'color_primary_4'
                }
            } else {
                return 'color_gray_4'
            }
        }
    }
}
</script>

<style scoped lang="scss">
.s {
    &-title {
        font-family: 'Pretendard Bold';
        font-size: 20px;
        color: #fff;
        line-height: 1.5;
        letter-spacing: -0.5px;
        margin: 16px 0 6px;
    }
    &-sub {
        font-family: 'Pretendard Medium';
        font-size: 14px;
        color: #777e8c;
        line-height: 1.43;
        word-break: keep-all;
    }
    &-notice {
        &-box {
            border-radius: 6px;
            background-color: #eee;
            padding: 7px;
            font-family: 'Pretendard Medium';
            font-size: 14px;
            margin-bottom: 4px;
            color: #777e8c;
            display: flex;
            align-items: center;
        }
        &-icon {
            width: 20px;
            height: 20px;
            margin-right: 4px;
        }
    }
    &-content {
        font-family: 'Pretendard';
        font-size: 24px;
        line-height: 1.5;
        letter-spacing: -0.5px;
        text-align: left;
        color: #777e8c;
        word-break: keep-all;
    }
}

</style>